@value Variables: "../../styles/Variables.css";
@value secondary-color from Variables;

.BusinessSettings {
  padding-top: 5rem;
  padding-left: 3rem;
}

.uploadSection {
  width: 100%;
  margin-bottom: 2rem;
}

.textArea {
  height: 15vh;
  width: 58%;
  border-radius: 3px;
  padding-top: 1rem;
  padding-left: 1rem;
  font-family: inherit;
  font-size: inherit;
  border: 1px solid secondary-color;
  outline: none;
}
.dzCont,
.prevCont {
  float: left;
  width: 50%;
}
.prevContInside {
  position: relative;
  /* top:-30px; */
  left: 50px;
}
.clearfix {
  clear: both;
}
